import React, { useState, useCallback } from "react";
import { postSendDownloadLink, regexPhone } from "../lib/utils";
import styleVars from "../styles/style-vars";
import { Link } from "gatsby";

const CallToAction = ({ onButtonClick }) => {
  const [phone, setPhone] = useState("+1");
  const [isError, setIsError] = useState(false);
  const [isLoading, setLoading] = useState(false);
  const [isSuccess, setIsSuccess] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");

  const onChange = value => {
    setErrorMsg("");
    setIsError(false);
    setIsSuccess(false);
    setPhone(value ? `+1${value.slice(2)}` : "+1");
  };

  const onSubmit = async () => {
    setIsSuccess(false);
    setIsError(false);
    var error = false;
    if (!phone || regexPhone.test(phone) === false) {
      error = true;
      setIsError(true);
      setErrorMsg("Please enter a valid contact number");
    }

    if (error === true) return;

    setLoading(true);
    const data = {
      phone_number: phone?.replace("+1", ""),
    };

    try {
      var res = await postSendDownloadLink(data);
      setLoading(false);
      if (res.ok) {
        setIsSuccess(true);
      } else if (res.status === 400) {
        setIsError(true);
        setErrorMsg("Please enter a valid contact number.");
      } else {
        setIsError(true);
        setErrorMsg("Please try again later.");
      }
    } catch (error) {
      setLoading(false);
      setIsError(true);
      setErrorMsg("Please try again later.");
    }
  };

  return (
    <div className="cta-container">
      <div
        className={`${styleVars.page.xl} pt8 pb8 pt20-ns pb20-ns mt6 mt16-ns`}
      >
        <div className="pt16">
          <div className="center" style={{ maxWidth: 800 }}>
            <h1 className="ma0 pa0 f2 f1-ns tc fw-bold">
            Start your first contribution now
            </h1>
            <div
            style={{
              display: "flex",
              justifyContent: "center",
            }}>
            <Link to="/athletes#laxmi">
            <button
                className="btn-base btn-contained f5"
                style={{
                  minWidth: 180,
                  height: 60,
                  padding: "0 20px",
                  marginTop: "40px",
                }}
              >
                Support Our Athletes
              </button>
            </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CallToAction;
