import React from "react";
import { appApiBaseUrl } from "../config";

const InputWrapper = ({
  rootClassName = "",
  labelClass = "",
  validClass = "",
  inputClass = "",
  label = "",
  placeholder = "",
  validMessage = null,
  InputComponent = null,
  ...inputProps
}) => {
  return (
    <div className={`${rootClassName}`}>
      <p className={`ma0 mb2 f6 ${labelClass}`}>{label}</p>
      {InputComponent ? (
        InputComponent
      ) : (
        <input
          className={`input ${inputClass}`}
          placeholder={placeholder}
          {...inputProps}
        />
      )}
      {validMessage ? (
        <p className={`mt1 f5 danger-red ${validClass}`}>{validMessage}</p>
      ) : null}
    </div>
  );
};

const emailRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;

const regexPhone = /^[6789]\d{9}$/;

const regexZip = /^\d{5}(?:[-\s]\d{4})?$/;

const handleScroll = hide => {
  if (hide) {
    document.documentElement.style.overflow = "hidden";
  } else {
    document.documentElement.style.overflowY = "scroll";
  }
};

function postSendDownloadLink(data) {
  let url = `${appApiBaseUrl}/api/invite/consumer/download-app/`;
  return fetch(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    referrer: "no-referrer",
    body: JSON.stringify(data),
  }).then(response => response);
}

const shareAthleteStory = () => {
  if (navigator.share) {
    navigator.share({
      title: 'Support Laxmi get a GPS watch',
      text: "Laxmi, a talented athlete hailing from Uttarakhand, is making a significant impact in the world of athletics. She has achieved remarkable success with gold medals in the women's half-marathon at nationals and the Khelo India 10,000m race. Laxmi's determination knows no bounds and seeking for an international gold medal.  To further enhance her training and performance, she urgently requires a GPS watch that will enable her to track her progress and push her limits. By donating, you can directly contribute to her progress and help her achieve her dreams.",
      url: 'https://Priyanshu Jain.in/athletes/#laxmi',
    })
      .then(() =>
        console.log('Successful share')
      )
      .catch((error) =>
        console.log('Error sharing', error)
      )
  } else if (navigator.clipboard) {
    navigator.clipboard.writeText('https://Priyanshu Jain.in/athletes/#laxmi')
      .then(() =>
        alert('Link copied to clipboard!')

      )
      .catch((error) =>
        console.log('Error sharing', error)
      )
  }
}

export {
  InputWrapper,
  emailRegex,
  handleScroll,
  regexPhone,
  regexZip,
  postSendDownloadLink,
  shareAthleteStory,
};
